.sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.35; /* its take space of 35% */
}
.sidebar_header{
    display: flex;
    justify-content: space-between;
    /* main parent (app_body) also have display flex so its not contain full space we requier flex : (%) ... on its parent (sidebar) */
    padding: 20px;
    border-right: 1px solid lightgray;
}
.sidebar_headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar_headerRight >.MuiSvgIcon-root{
    /* mui class */
    margin-right: 2vw;
    font-size: 24px !important;
}
.sidebar_search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
    
}
.sidebar_searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    height: 35px;
    width: 100%;
    border-radius: 20px;
}
.sidebar_searchContainer > input{
    border: none;
    margin-left: 10px;
    outline: none;
}
.sidebar_searchContainer > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}
.sidebar_chats{
    flex: 1; /* occupie full lenght */
    background-color: white;
    overflow: auto;
}